.filter {
  display: block;
  margin-bottom: 20px;
  font-size: 20px;
}

.filter-input {
  display: block;
  width: 155px;
  margin-top: 5px;
  border: 2px solid silver;
  font-size: 12px;
}

.filter-input:hover,
.filter-input:focus {
  outline: none;
  border: 2px solid skyblue;
}
